.leaflet-bar a,
.leaflet-bar a.leaflet-disabled {
  background-color: #232c34;
  color: white;
  box-shadow: none;
  border-radius: 0px;
}

.leaflet-bar a:hover,
.leaflet-bar a.leaflet-disabled:hover {
  background-color: #232c34;
  color: white;
  border-radius: 0px;
}

.leaflet-touch .leaflet-control-layers,
.leaflet-touch .leaflet-bar {
  border: none;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.9), 0 2px 4px 0 rgba(0, 0, 0, 0.12);
}
